.login-form {
  max-width: 350px;
  margin: 0px auto;
  padding: 0px 0px;
  position: relative;
}

.login-form img {
  max-width: 250px;
  margin: 0px auto;
  display: block;
}
.head-logo img {
  max-width: 100px;
  margin: 0px auto;
  display: block;
  border: 2px solid#018bcf;
  border-radius: 10px;
  padding: 5px;
  margin-bottom: 20px;
}

.login-form h1 {
  font-size: 32px;
  text-align: center;
  color: #fff;
}

.login-form p {
  text-align: center;
  color: #99a4b0cc;
  font-size: 14px;
}

.login-form h1 span {
  color: var(--theme-color-main);
}

.login-box {
  padding-top: 15px;
  max-width: 400px;
  margin: 0px auto;
}

.login-box .form-group .form-control {
  height: auto;
  padding: 8px 15px;
  background: var(--primary-600) !important;
  border: 1px solid var(--primary-400);
  border-radius: 30px;
  font-size: 14px;
  color: #99a4b0cc;
}

.login-box .form-group .form-control::placeholder {
  color: #99a4b0cc;
}

.login-box .form-group .form-control:focus {
  box-shadow: none;
  background: var(--primary-400);
}

.login-box input:-internal-autofill-selected {
  background: transparent !important;
}

.login-box .form-group {
  margin-bottom: 15px;
}

.l-btn.btn.btn-primary {
  padding: 8px 40px;
  font-size: 14px;
  background: var(--theme-color-main);
  border-radius: 30px;
  font-weight: 600;
  color: var(--primary-base);
  border: none;
  transition: all 0.5s;
  width: 100%;
  margin-top: 15px;
}

.l-btn.btn.btn-primary:hover,
.l-btn.btn.btn-primary:focus,
.l-btn.btn.btn-primary:active {
  background: var(--theme-color-main);
  box-shadow: none;
  border: none;
}

.l-btn.btn.btn-primary:hover {
  filter: drop-shadow(0px 0px 5px var(--theme-color-main));
}

.login-button {
  text-align: center;
}

.login-box p {
  text-align: center;
  padding-top: 20px;
  font-size: 14px;
  color: #99a4b0cc;
  margin: 0;
}

.signup-link {
  color: var(--theme-color-main);
  text-decoration: underline;
}

.signup-link:hover {
  color: #c5c5c5;
}

.forget-link {
  color: var(--theme-color-main);
  font-size: 13px;
}

.forget-link:hover {
  color: #99a4b0cc;
  text-decoration: none;
}

.forget-password {
  padding-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.remember-me .form-check-label {
  font-weight: 400;
  font-size: 14px;
  color: #99a4b0cc;
}

.social-login {
  text-align: center;
}

.social-login span {
  color: #afb4d0;
  display: block;
  padding: 10px 0px;
  font-size: 12px;
}

.social-btns {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.social-btns .btn {
  width: 48%;
  font-size: 14px;
}

.facebook-btn {
  background: #3b5998 !important;
  border-color: #3b5998 !important;
}

.twitch-btn {
  background: #4b367c !important;
  border-color: #4b367c !important;
}

.login {
  position: relative;
}

.login-popup .modal-header {
  border-bottom: none;
  padding-bottom: 0;
}

.login-popup .form-label {
  font-size: 14px;
  color: #fff;
  font-weight: 500;
  margin-bottom: 5px;
}

.login-popup .modal-header .close:focus {
  outline: none;
}

.login-popup .close {
  color: #99a4b0cc;
  text-shadow: none;
  font-weight: 400;
  font-size: 30px;
  cursor: pointer;
}

.login-popup .close:hover {
  color: #efebeb;
}

.modal-backdrop {
  background-color: #3b3b3b !important;
  opacity: 0.9 !important;
}

.login-popup .modal-body {
  padding-top: 0;
}

.login-popup .modal-content {
  position: relative;
  background-size: cover;
  background-position: center;
  position: relative;
  background: var(--primary-base);
}


.login-popup .modal-dialog{
  max-width: 350px;
  margin: 0.5rem auto;
}

.btn-close{
  filter: invert(1);
}
.btn-close:focus{
  box-shadow: none!important;

}

.forget-link,
.forget-link:focus,
.forget-link:hover {
  background: transparent !important;
  border: none !important;
  font-weight: 400 !important;
  font-size: 14px !important;
  color: var(--theme-color-main) !important;
  box-shadow: none !important;
  padding: 0 !important;
}

.forget-link.btn-primary:not(:disabled):not(.disabled).active:focus,
.forget-link.btn-primary:not(:disabled):not(.disabled):active:focus {
  box-shadow: none !important;
}
