.header{
  position: sticky;
  padding: 10px 0px 10px;
  top: 0;
  background: var(--primary-800);
  z-index: 9;
}

.header:after {
  content: "";
  position: absolute;
  left: 0px;
  bottom: -0.75rem;
  width: 100%;
  height: 0.75rem;
  background-image: linear-gradient(rgb(17, 20, 21), rgba(36, 38, 43, 0));
  opacity: 0.25;
}

.header-grid {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
}

.logo h2 {
    margin: 0;
}


.logo img {
    max-width: 225px;
}
.header-menu-section {
  width: calc(100% - 230px);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 20px;
}
.animated-logo {
    position: relative;
}

.animated-logo .animate-icon {
    max-width: 100px;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    -webkit-animation: mover 1s infinite alternate;
    animation: mover 1s infinite alternate;
}



@-webkit-keyframes mover {
    0% {
      transform: translateX(-50%) translateY(0);
    }
    100% {
      transform: translateX(-50%) translateY(-10px);
    }
  }
  @keyframes mover {
    0% {
      transform: translateX(-50%) translateY(0);
    }
    100% {
      transform: translateX(-50%) translateY(-10px);
    }
  }

  

.auth-box .btn{
  background: var(--theme-color-main);
  border-radius: 30px;
  border-color: var(--theme-color-main);
    font-size: 14px;
    color: #151823;
    margin-left: 0;
    max-width: 100px;
    height: 39px;
    width: 100px;
    padding: 8px 10px;
    box-shadow: none;
    font-weight: 500;
    transition: all 0.5s;
}

.auth-box .btn:hover, .auth-box .btn:focus {
  background: var(--theme-color-main);
  border-color: var(--theme-color-main);
  color: #151823;
  box-shadow: none;
 
}
.auth-box .btn:first-child:hover, .auth-box .btn:first-child:hover{
  background: var(--theme-color-main);
  border-color: var(--theme-color-main);
  color: #151823;
}

.auth-box {
    display: flex;
    flex-wrap: nowrap;
    max-width: fit-content;
    width: fit-content;
    width: fit-content;
    gap: 10px;
}

.auth-box .btn:first-child{
    background: transparent;
    color: var(--theme-color-main);
}
.auth-box .btn:hover{
  filter: drop-shadow(0px 0px 5px var(--theme-color-main));
}

.chat-mbl {
  display: none;
}



.user-loggedin {
  display: flex;
  align-items: center;
}

/* User Loggedin Dropdown */
.user-loggedin .dropdown-toggle {
  display: flex;
  gap: 8px;
  justify-content: flex-start;
  align-items: center;
  background: transparent !important;
  border: 0 !important;
  box-shadow: none !important;
  outline: 0 !important;
}
.user-loggedin .dropdown-toggle:after {
content: none;
}
.user-loggedin .dropdown-toggle img{
border-radius: 50%;
height: 28px;
width: 28px;
object-fit: cover;
}
.user-loggedin .dropdown-toggle span {
  font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: var(--text-light-400);
    opacity: 0.8;
}
.user-loggedin .dropdown-toggle:hover span,
.user-loggedin .dropdown-toggle:focus span {
  opacity: 1;
}
.user-loggedin .dropdown-toggle:hover,
.user-loggedin .dropdown-toggle:focus,
.user-loggedin .dropdown-toggle:focus-within {
  box-shadow: none !important;
  outline: 0 !important;
}

.user-loggedin .dropdown-menu {
  background: var(--primary-800);
  box-shadow: 0 0 25px rgb(0 0 0 / 25%);
  box-shadow: #0003 0 4px 6px -1px, #0000001f 0 2px 4px -1px;
  width: 150px;
  right: 0 !important;
  left: unset !important;
  border: 0;
  margin-top: 17px;
}

.user-loggedin .dropdown-menu .dropdown-item, .user-loggedin .dropdown-menu .dropdown-item-text {
  color: var(--text-light-400);
font-style: normal;
font-weight: 500;
font-size: 12px;
line-height: 1.5;
    padding: 8px 10px;
    display: flex;
  gap: 8px;
  justify-content: flex-start;
  align-items: center;
  border: 0 !important;
  box-shadow: none !important;
  outline: 0 !important;
  margin-bottom: 3px;
  cursor: pointer;
}
.user-loggedin .dropdown-menu .dropdown-item a {
  color: inherit !important;
  font-size: inherit !important;
  line-height: inherit !important;
}
.user-loggedin .dropdown-menu .dropdown-item:last-child {
  margin-bottom: 0;
}
.user-loggedin .dropdown-menu .iconSection {
  height: 18px;
  width:18px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.user-loggedin .dropdown-menu img {
  width: auto;
    height: 18px;
}
.user-loggedin .dropdown-menu img.logOut-img {
  height: 14px;
}
.user-loggedin .dropdown-menu svg {
  width: 14px;
  height: 14px;
}
.user-loggedin .dropdown-menu svg path {
  fill: var(--text-light-400);
}
.user-loggedin .dropdown-menu .dropdown-item:hover,
/* .user-loggedin .dropdown-menu .dropdown-item-text:hover, */
.user-loggedin .dropdown-menu .dropdown-item:focus,
.user-loggedin .dropdown-menu .dropdown-item:focus-within,
.user-loggedin .dropdown-menu .dropdown-item:hover svg path,
/* .user-loggedin .dropdown-menu .dropdown-item-text:hover svg path, */
.user-loggedin .dropdown-menu .dropdown-item:focus svg path,
.user-loggedin .dropdown-menu .dropdown-item:focus-within svg path {
  color: #fff;
  fill: #fff;
  background: transparent;
  cursor: pointer;
}

.user-pic {
  width: 40px;
}

.user-pic img {
  width: 40px;
  height: 40px;
  object-fit: cover;
  border-radius: 100%;
}

.user-info h4 {
  margin: 0;
  font-size: 14px;
}

.user-info p {
  font-size: 10px;
  margin: 0;
}

.user-info {
  padding-left: 10px;
}

.logout-btn {
  margin-left: 20px;
  cursor: pointer;
}

/* Wallet Section */
.header-wallet-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  border-radius: 30px;
  border: 2px solid var(--primary-base);
  height: 35px;
  background: #26282e;
}
span.wallet-amt {
  font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #fff;
    padding: 5px 5px 5px 10px;
    max-width: 100px;
    overflow:hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
span.la-wallet {
  font-size: 20px;
  width: 35px;
  height: 35px;
  background: var(--primary-base);
  overflow: hidden;
  padding: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0 30px 30px 0;
  margin-right: -1px;
}


.chat-mbl {
  /* position: absolute; */
  right: 10px;
  width: 45px;
  height: 35px;
  background: var(--primary-base);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
  top: 18px;
  transition: 0.4s all ease;
  cursor: pointer;
}

.chat-mbl img,
.chat-mbl svg {
  width: 20px;
}

.chat-mbl svg path {
  stroke: var(--text-light-400);
}
.chat-mbl:hover svg path {
  stroke:#cacaca;
}


/************************** Meadia Query **************************/

@media screen and (max-width:767px){

.header-grid {
  justify-content: space-between;
}
.logo img {
  max-width: 200px;
}
/* .auth-box .btn {
  margin: 0px 5px;
} */

.auth-box {
  max-width: 100%;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 10px;
}

/* .wrapper-hide .chat-mbl {
right: 310px;
} */

}


@media (max-width: 576px) {
  .logo img {
    max-width: 170px;
  }
  .header-menu-section {
    gap: 10px;
    width: calc(100% - 170px);
  }
  .auth-box{
    padding: 0;
  }
  .auth-box .btn {
    width: 90px;
  }
  .header-menu-section {
    gap: 5px;
  }
  .user-loggedin .dropdown-toggle img {
    height: 25px;
    width: 25px;
  }
  .user-loggedin .dropdown-toggle {
    padding: 5px;
  }
  .header-wallet-section {
    gap: 5px;
    height: 30px;
  }
  span.la-wallet {
    width: 30px;
    height: 30px;
    font-size: 18px;
  }
  span.wallet-amt {
    font-size: 13px;
    opacity: 0.8;
    max-width: 60px;
  }
}

@media (max-width: 480px) {
  .logo img {
    max-width: 120px;
  }
  .header-menu-section {
    width: calc(100% - 120px);
  }
  .auth-box .btn {
    width: 65px;
    height: 30px;
    padding: 5px;
    font-size: 12px;
  }
  .chat-mbl {
    height: 30px;
    width: 35px;
  }
  .chat-mbl img {
    width: 18px;
  }
}