.legal-page {
    padding: 50px 0px;
    font-family: 'Poppins', sans-serif !important;
    min-height: calc(100vh - 271px);
}

.legal-page h1,
.legal-page h2,
.legal-page h3,
.legal-page h4,
.legal-page h5,
.legal-page h6 {
    font-weight: 600;
    color: #ededed;
}

.legal-page h1 {
    font-size: 28px;
    line-height: 20px;
    margin-bottom: 0;
    padding-bottom: 40px;
    text-transform: uppercase;
}

.legal-page h2 {
    font-size: 24px;
    line-height: 20px;
    margin-bottom: 0;
    padding-bottom: 40px;
    text-transform: uppercase;
}

.legal-page h3 {
    font-size: 22px;
    line-height: 1.2;
}

.legal-page h4 {
    font-size: 20px;
    line-height: 1.2;
}

.legal-page h5 {
    font-size: 18px;
    line-height: 1.2;
    margin-bottom: 15px;
}

.legal-page h6 {
    font-size: 16px;
    line-height: 1.2;
}

.legal-page p,
.legal-page a,
.legal-page ul li,
.legal-page ol li {
    font-size: 14px;
    line-height: 1.4;
    color: #99a4b0;
}

.legal-page a,
.legal-page a:hover,
.legal-page a:visited,
.legal-page a:active,
.legal-page span {
    text-decoration: none !important;
    font-weight: 600;
    color: var(--theme-color-main);
}

.legal-page ul.removeIcon {
    list-style-type: none;
}

.legal-page ul.circle {
    list-style-type: circle;
}

.legal-page ul.disc {
    list-style-type: disc;
}

.legal-page ul.square {
    list-style-type: square;
}

.legal-page ul li,
.legal-page ol li {
    margin-bottom: 0.5rem;
}

.legal-page ul li::marker,
.legal-page ol li::marker {
    font-weight: 600;
}


/* FAQ Page */

.faq-section .accordion .accordion-item {
    background: transparent;
}

.faq-section .accordion .accordion-item {
    background: transparent;
    border: 0px solid #31343c;
    border-radius: 10px;
    margin-bottom: 10px;
}

.faq-section .accordion .accordion-item .accordion-header {
    padding-bottom: 0;
}

.faq-section .accordion .accordion-item .accordion-header button,
.faq-section .accordion .accordion-item .accordion-header button:hover
.faq-section .accordion .accordion-item .accordion-header button:focus,
.faq-section .accordion .accordion-item .accordion-header button:focus-within {
    background: var(--primary-800);
    color: #fff;
    font-weight: 600;
    font-size: 14px;
    box-shadow: none !important;
    outline: 0 !important;
    overflow: hidden;
    border: 0;
}
.faq-section .accordion .accordion-item .accordion-header button:not(.collapsed){
    border-radius: 8px 8px 0 0;
}
.faq-section .accordion .accordion-item .accordion-header button.collapsed {
    border-radius: 8px;
}
/* .faq-section .accordion .accordion-item .accordion-header button:hover {
    color: var(--theme-color-main);
} */

.faq-section .accordion .accordion-item .accordion-header .accordion-button::after {
    background-image: none;
    font-family: 'Line Awesome Free';
    font-weight: 900;
    content: "\f067";
    color: var(--theme-color-main);
    font-size: 20px;
}

.faq-section .accordion .accordion-item .accordion-header .accordion-button:not(.collapsed)::after {
    background-image: none;
    font-family: 'Line Awesome Free';
    font-weight: 900;
    content: "\f068";
}

.faq-section .accordion .accordion-item .accordion-header button:hover:after,
.faq-section .accordion .accordion-item .accordion-header button:not(.collapsed)::after {
    filter: drop-shadow(0 0 4px var(--theme-color-main));
}

.faq-section .accordion .accordion-item .accordion-body {
    background: transparent;
    border-top: 0px solid var(--primary-800);
    border: 2px solid var(--primary-800);
    border-top: 0;
    border-radius: 0 0 8px 8px;
}

.faq-section .accordion .accordion-item .accordion-body p {
    margin: 0;
}
.faq-section .accordion .accordion-item .accordion-body a {
    color: var(--theme-color-main) !important;
    text-decoration: none !important;
    padding: 16px 20px;
}


/************************************** Media Query *********************************/
@media (max-width:576px) {
.legal-page h1 {
    line-height: 1.2;
    font-size: 24px;
}
.legal-page p, .legal-page a, .legal-page ul li, .legal-page ol li {
    font-size: 13px;
}
.faq-section .accordion .accordion-item .accordion-header button,
.faq-section .accordion .accordion-item .accordion-header button:hover
.faq-section .accordion .accordion-item .accordion-header button:focus,
.faq-section .accordion .accordion-item .accordion-header button:focus-within {
    font-size: 13px;
}
}