.chat-wrapper {
  width: 350px;
  background: var(--primary-600);
  /* background: linear-gradient(90deg,rgba(38,40,47,.75),rgba(26,28,34,.75)),#26282f; */
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  transition: 0.4s all ease;
  box-shadow: rgb(0 0 0 / 20%) 0px 0px 10px 0px;
  overflow: hidden;
}

.chat-wrapper.chat-hide {
  right: -350px;
}

.chat {
  display: flex;
  justify-content: center;
  height: 100%;
}

.chat-footer h2 {
  text-align: left;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 30px;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  color: #99a4b0cc;
  position: relative;
  padding-left: 35px;
  margin: 0;
  margin-top: 12px;
}

.chat-footer h2::before {
  content: "";
  background: url(../../assets/images/chat/online.svg);
  width: 16px;
  height: 16px;
  display: block;
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
  left: 12px;
  top: 50%;
  transform: translateY(-50%);
}

.hide-chat-btn {
  cursor: pointer;
}

.hide-chat-btn {
  position: absolute;
  left: -35px;
}
.chat-view span img {
  width: 20px;
  cursor: pointer;
  opacity: 0.5;
}

.hide-btn {
  width: 30px;
  height: 30px;
  background: #191c24;
  text-align: center;
  line-height: 30px;
  border-radius: 100%;
  cursor: pointer;
  display: none;
}

.chat-view {
  width: 100%;
}

.chat-content {
  padding: 0px 0px;
}

.container1 {
  margin-bottom: 8px;
}

.container1::after {
  content: "";
  clear: both;
  display: table;
}

.imgleft {
  float: left;
  max-width: 60px;
  width: 100%;
  margin-right: 0px;
  border-radius: 50%;
}

.imgright {
  max-width: 60px;
  width: 100%;
  margin-right: 0px;
  border-radius: 50%;
}
.time-left {
  float: left;
  color: #ddd;
}

.time-right {
  float: right;
  color: #ddd;
}

.chat-user {
  display: flex;
  padding: 0px 15px;
}

.chat-user img {
  max-width: 38px;
  width: 38px;
  height: 38px;
  margin-right: 0px;
  border-radius: 28px;
  object-fit: cover;
}

.chat-user-info h4 {
  font-weight: 600;
  font-size: 14px;
  letter-spacing: 0.3px;
  line-height: 23px;
  color: white;
  margin: 0;
}

.chat-user-info .time {
  margin-left: 0.5rem;
  color: rgba(153, 164, 176, 0.6);
  font-size: 0.75rem;
}

.chat-user span {
  margin: 0;
  box-sizing: border-box;
  max-width: 100%;
  overflow-wrap: break-word;
  font-weight: 400;
  letter-spacing: 0.3px;
  color: rgb(202, 209, 217);
  -webkit-user-select: text;
  user-select: text;
  font-size: 0.875rem;
}

.chat-user .nickname {
  background: var(--primary-600);
  color: white;
  padding: 2px 8px;
  border-radius: 4px;
  margin-right: 5px;
  font-weight: 600;
  font-size: 14px;
}

.container1 .time-right {
  float: none;
  font-size: 10px;
  letter-spacing: 1px;
  color: #b5b5b5;
  display: block;
  text-align: left;
  padding: 0;
  border-radius: 30px;
}

.container1 .time-left {
  float: none;
  font-size: 8px;
  letter-spacing: 1px;
  color: #b5b5b5;
  display: block;
  text-align: right;
  padding: 0;
  border-radius: 30px;
}

.chat-box {
  height: calc(100vh - 149px);
  overflow-y: auto;
  margin-bottom: 0px;
  padding: 10px 0px;
}

.chat-footer {
  padding: 15px 15px;
  background: var(--primary-600);
  /* background-color: rgba(49, 52, 58, 0.8); */
  box-shadow: #0003 6px 4px 6px 2px, #0000001f 2px 0 4px 2px;
  /* border-top: 1px solid var(--primary-400); */
  border-top: 1px solid var(--text-light-400);
}
.chat-footer form {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.chat-footer input {
  padding-right: 40px;
}
.no-chat {
  padding: 100px 20px;
  text-align: center;
  color: #ddd;
}
.chat-footer .btn,
.chat-footer .btn:hover,
.chat-footer .btn:focus {
  background: var(--theme-color-main);
  border-radius: 30px;
  border-color: var(--theme-color-main);
  font-size: 14px;
  margin-left: 5px;
  max-width: 60px;
  width: 100%;
  padding: 8px 10px;
  box-shadow: none;
  font-weight: 500;
  transition: all 0.5s;
  height: 40px;
  display: flex;
    align-items: center;
    justify-content: center;
}
.chat-footer .btn:hover {
  filter: drop-shadow(0px 0px 5px var(--theme-color-main));
}

.chat-footer .btn img {
  width: 22px;
}
.chat-footer .btn svg {
  width: 22px;
}

.chat-footer .btn-primary:not(:disabled):not(.disabled):active:focus {
  background: var(--theme-color-main);
  border-color: var(--theme-color-main);
  box-shadow: none;
}
.chat-user.left-view {
  flex-direction: row-reverse;
}

.chat-user.left-view p {
  text-align: right;
}
.chat-user.left-view span {
  text-align: right;
}

.chat-user.left-view {
  flex-direction: row-reverse;
}

.chat-user.left-view img {
  margin-left: 0px;
  margin-right: 0;
}

.chat-user.left-view p {
  text-align: right;
}

.chat-user .chat-user-pic {
  width: 40px;
  margin-right: 10px;
}

.chat-user .chat-user-info {
  width: 100%;
  background: var(--primary-800);
  border-radius: 4px;
  padding: 0.625rem;
}

.chat-user.left-view .chat-user-pic {
  margin-left: 10px;
  margin-right: 0;
}

.chat-box::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #191c24;
}

.chat-box::-webkit-scrollbar {
  width: 0px;
  background-color: #191c24;
}

.chat-box::-webkit-scrollbar-thumb {
  background-color: #191c24;
}

.chat-footer .form-control,
.chat-footer .form-control:focus {
  background: var(--primary-500);
  border: 1px solid var(--primary-base);
  box-sizing: border-box;
  box-shadow: 0px 4px 9px rgb(0 0 0 / 15%);
  border-radius: 30px;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  color: #99a4b0cc;
  height: 44px;
  padding: 8px 20px;
  padding-right: 45px;
}

.chat-footer .form-control::placeholder {
  color: #99a4b0cc;
}

.mobile-icon {
  display: none;
}

.hide-btn i.fa.fa-envelope {
  font-size: 14px;
  position: relative;
  top: -2px;
}
.chat-input {
  position: relative;
  width: calc(100% - 65px);
}
.chat-input img {
  position: absolute;
  right: 15px;
  top: 15px;
}

.chat-wrapper .all-bets {
  padding: 0px 0px;
  position: relative;
}

.chat-wrapper .all-bets .nav-tabs .nav-item {
  max-width: 50%;
}

.chat-wrapper .all-bets .nav-tabs {
  position: relative;
  max-width: 80%;
  margin: 14px auto 14px 14px;
}

.chat-wrapper .all-bets .nav-tabs:after {
  content: "";
  position: absolute;
  left: -9px;
  bottom: -1.5rem;
  width: 106%;
  height: 0.75rem;
  background-image: linear-gradient(rgb(17, 20, 21), rgba(36, 38, 43, 0));
  opacity: 0.25;
}

.chat-wrapper .all-bets .nav-tabs .nav-link {
  padding: 8px 8px;
}

.chat-wrapper .all-bets .nav-tabs .nav-link img {
  width: 20px;
}

.group-chat-box {
  height: calc(100vh - 171px);
  overflow-y: auto;
  margin-bottom: 0px;
  padding: 0px;
}

.group-chat-user {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 15px 15px;
  border-bottom: 1px dashed var(--primary-400);
  cursor: pointer;
  transition: all 0.5s;
}

.group-chat-user:hover {
  background: var(--primary-800);
  background: linear-gradient(90deg,var(--primary-600),var(--primary-400)),var(--primary-800);
}

.group-chat-pic {
  width: 40px;
  height: 40px;
  background: var(--primary-400);
  border-radius: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.group-chat-user:hover .group-chat-pic {
  background: var(--primary-400);
}

.group-chat-user-info {
  width: calc(100% - 50px);
}

.group-chat-user-info h4 {
  font-weight: 600;
  font-size: 14px;
  letter-spacing: 0.3px;
  line-height: 21px;
  color: rgb(202, 209, 217);
  margin: 0;
}

.group-chat-user-info span {
  font-size: 12px;
  color: rgba(255, 255, 255, 1);
  display: block;
}

.group-chat-pic img {
  width: 18px;
}

.group-chat-user:last-child {
  border: none;
}

.group-chat .chat-box {
  padding: 0;
}

span.back-chat {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 5px 15px;
  color: rgba(153, 164, 176, 0.6);
  font-size: 14px;
  position: sticky;
  top: 0;
  background: var(--primary-600);
  margin: 0px 15px;
  cursor: pointer;
}

span.back-chat img {
  width: 15px;
}

.chat-paused {
  position: absolute;
  display: flex;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  align-items: center;
  justify-content: center;
  background: var(--primary-600)ed;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  color: #99a4b0cc;
}

.create-group-btn {
  position: absolute;
  bottom: 20px;
  display: flex;
  justify-content: center;
  width: 100%;
}

.create-group-btn button {
  background: var(--theme-color-main) !important;
  border-radius: 30px;
  border-color: var(--theme-color-main);
  font-size: 14px;
  color: #151823;
  max-width: 150px;
  width: 100%;
  padding: 12px 10px;
  box-shadow: none;
  font-weight: 500;
  transition: all 0.5s;
}
.create-group-btn button:hover, .create-group-btn button:focus, .create-group-btn .btn:active {
  background-color: var(--theme-color-main) !important;
  color: var(--primary-base) !important;
  border-color: var(--theme-color-main) !important;
  filter: drop-shadow(0px 0px 5px var(--theme-color-main));
  outline: none;
  box-shadow: none;
}

/********** create group popup **********/
.create-group-popup .react-select__multi-value {
  background: var(--primary-400) !important;
}
.create-group-popup .react-select__multi-value__remove:hover{
  background-color: var(--primary-400) !important;
}
.create-group-popup .form-group .form-control {
  height: auto;
  padding: 8px 15px;
  background: var(--primary-600) !important;
  border: 1px solid var(--primary-400);
  border-radius: 8px;
  font-size: 14px;
  color: #99a4b0cc;
  width: 100%;
  height: 40px;
}
.create-group-popup  .form-group {
  margin-bottom: 25px !important;
}
.create-group-popup  .modal-dialog {
  max-width: 450px;
}
.create-group-popup .create-group-content {
  margin: 20px 12px 0px;
}
.create-group-popup .submit-btn button {
  background: var(--theme-color-main);
  border-radius: 30px;
  border-color: var(--theme-color-main);
  font-size: 14px;
  color: #151823;
  max-width: 150px;
  width: 100%;
  padding: 12px 10px;
  box-shadow: none;
  font-weight: 500;
  transition: all 0.5s;
}
.create-group-popup .submit-btn {
  margin: 25px 0px 10px;
  display: flex;
  justify-content: center;
}
.invite-people * {
  color: #90929a !important;
  font-size: 13px !important;
  /* background-color: #262730 !important; */
}
.invite-people label.form-label {
  color: #fff !important;
}
.invite-people {
  width: 100%;
}
/* .invite-people span {
  display: none;
} */

/* Close Button */
.close-chats {
  padding: 5px !important;
  display: flex !important;
  align-items: center;
  justify-content: center;
  border:0 !important;
  background: transparent !important;
  opacity: 0.8;
  position: absolute;
  right: 14px;
  top: 4px;
}
.close-chats:hover, .close-chats:focus, .close-chats:focus-within {
  box-shadow: none !important;
  outline: 0 !important;
  background: transparent !important;
  opacity: 1;
}
.close-chats img {
  width: 26px;
  height: 26px;
  transform: rotate(180deg);
}
.close-chats svg {
  width: 24px;
  height: 24px;
  transform: rotate(180deg);
}
.close-chats svg path{
  fill: var(--text-light-400);
}
.close-chats:hover svg path, .close-chats:focus svg path, .close-chats:focus-within svg path {
fill: var(--text-light-400);
}




/************************** Meadia Query **************************/

@media screen and (max-width: 767px) {
  /* .chat-wrapper {
    right: -350px;
    width: 350px;
  } */
  .wrapper {
    width: 100%;
    margin: 0px auto;
    margin-left: 0;
  }


  .hide-chat-btn {
    display: none;
  }
}

@media screen and (max-width: 576px) {
  .chat-wrapper {
    right: 0;
    width: 100%;
  }
  
  .chat-wrapper.chat-hide {
    right: -100%;
  }
}