.footer {
    padding: 50px 0px;
    text-align: center;
    position: relative;
    background: var(--primary-800);
    overflow: hidden;
}
.footer-grid {
    display: flex;
   flex-direction: column;
   gap:20px;
}
.footer h2 {
    margin: 0;
    padding-bottom: 10px;
}
.footer-row {
    display: grid;
    grid-template-columns: calc(50% - 30px) 25% 25%;
    justify-content: space-between;
    gap: 15px;
    padding-bottom: 20px;
    border-bottom: 1px solid var(--primary-400);
}
.footer-col {
    width: calc(100% - 250px);
    display: flex;
    gap: 20px;
    justify-content: flex-end;
}

.footer p {
    font-size: 14px;
    margin: 0;
    padding: 10px 0;    
    color: var(--text-light-500);
    /* padding-bottom: 10px; */
}
.footer p.footer-links {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    text-align: left;
    gap: 15px;
}
.footer a, .footer a:hover, .footer a:active, .footer a:visited {
    font-size: 14px;
    margin: 0;
    padding-bottom: 10px;
    color: var(--text-light-500);
    text-decoration: none;
    display: flex;
    align-items: center;
}
.footer a:hover {
    color: var(--theme-color-main);
    filter: drop-shadow(0 0 4px var(--theme-color-main));
    text-decoration: underline;
}
.footer-links a {
    justify-content: flex-start;
    text-align: left;
}
.footer p span {
    color: var(--theme-color-main);
}
.logo {
    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: space-around;
}
.footer img {
    max-width: 200px;
    margin: 0 auto 0 0;
    display: block;
    height: fit-content;
    /* margin-bottom: 20px; */
}


.footer ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: left;
    gap: 15px;
}

.footer ul li {
    /* margin: 0px 0 10px; */
    margin: 0;
    font-size: 14px;
    color: var(--text-light-500);
}



.footer-links h5 {
    color: #ffffff;
    text-align: left;
    margin-bottom: 1em;
  }

/* .footer p.footer-links a:before {
    content: "|";
    color: var(--theme-color-main);
    margin: 0 10px;
} */
.footer p.footer-links a:first-child:before {
    content: none;
}

.social-links {
    max-width: 225px;
    justify-content: flex-start !important;
}

.social-links li a,
.social-links li a:hover,
.social-links li a:visited,
.social-links li a:active {
    padding: 0;
    font-size: 30px;
    color: #ffffff;
    text-decoration: none;
}
.social-links li a .la-facebook-f {
    font-size: 24px;
}
.social-links li a:hover {
    color: var(--theme-color-main);
}

.footer-end a,
.footer-end a:visited {
    color: var(--theme-color-main);
}





@media (max-width: 786px) {
    /* .footer-row {
        grid-template-columns: calc(40% - 30px) 30% 30%;
    } */
    .footer-end a,
.footer-end a:visited,
.footer-end a:hover,
.footer-end a:active {
    padding-bottom: 0;
}
}

@media (max-width: 576px) {
    .footer-row {
        display: flex;
        flex-wrap: wrap;
    }
    .footer-row .logo img {
        margin: 0;
        margin-left: 0;
        width: calc(50% - 8px);
    }
    .social-links {
        justify-content: flex-end !important;
    }
    .footer-row .logo {
        width: 100%;
        align-items: center;
        padding-bottom: 15px;
        flex-direction: row;
        justify-content: space-between;
    }
    .footer-links {
        width: calc(50% - 8px);
    }
    .footer-links h5 {
        font-size: 1rem;
    }
    .footer-links a, .footer p, .footer ul li, .footer-end a {
        font-size: 0.7rem;
    }
    .footer p {
        padding: 0;
    }
    
    .footer-links:last-child h5{
            text-align: right;
    }
    .footer-links:last-child a {
            justify-content: flex-end;
    }
    .social-links li a,
.social-links li a:hover,
.social-links li a:visited,
.social-links li a:active {
    font-size: 26px;
}
.social-links li a .la-facebook-f {
    font-size: 20px;
}
}