/********** profile wrapper css **************/
.profile-wrapper {
  padding: 50px 0px;
}
.profile-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 20px;
}

.profile-sidebar {
  width: 298px;
}

.profile-content {
  width: calc(100% - 320px);
  background: var(--primary-600);
  border-radius: 5px;
}

.profile-menu {
  background: var(--primary-600);
  border-radius: 3px;
  padding: 20px;
  margin-bottom: 24px;
  min-height: 450px;
  height: 100%;
  /* box-shadow: rgb(0 0 0 / 20%) 0px 0px 10px 0px; */
}

.profile-user {
  background: #16202b;
  border-radius: 3px;
  padding: 20px;
  margin-bottom: 24px;
}

.page-heading {
  border-radius: 3px 3px 0px 0px;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #d7d7d70f;
  background:  var(--primary-800);
}

.page-heading h3 {
  margin: 0;
  font-weight: 600;
  font-size: 16px;
  line-height: 18px;
  color: #fff;
  display: flex;
  align-items: center;
}
.user-profile .form-group:last-child {
  width: 100% !important;
}
.page-heading h3 img {
  margin-right: 6px;
}

.user-name span {
  margin-left: 10px;
  text-align: center;
  display: inline-block;
  color: #99a4b0cc;
  font-weight: 600;
}

.user-name {
  font-weight: normal;
  font-size: 18px;
  line-height: 20px;
  color: #ffffff;
  display: flex;
  align-items: center;
}

.user-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
}

.logout {
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  color: #99a4b0cc;
  cursor: pointer;
}
.logout img, .logout svg {
  height: 16px;
    opacity: 0.7;
    width: 16px;
}
.logout:hover img {
  opacity: 1;
}

.user-name img {
  object-fit: cover;
  border-radius: 50%;
  height: 50px !important;
  width: 50px !important;
}

.profile-level span {
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  color: #99a4b0cc;
}

.profile-level {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.profile-level span:last-child {
  color: rgba(124, 143, 160, 0.5);
}

.profile-complete {
  padding-top: 15px;
  padding-bottom: 10px;
}

.profile-complete .progress {
  height: 7px;
  background-color: #121b24;
  border-radius: 1px;
}

.profile-complete .progress-bar {
  background: #cda73b;
  border-radius: 1px;
}

.profile-menu h5 {
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  color: #99a4b0cc;
  margin: 0;
  padding-bottom: 15px;
}

.profile-menu .nav a {
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  color: #99a4b0cc;
  display: flex;
  width: 100%;
  padding: 15px 10px;
  margin-bottom: 5px;
  align-items: center;
  border-radius: 25px;
  text-decoration: none;
}

.profile-menu .nav a:hover {
  /* background: #31343acc; */
  background: linear-gradient(90deg,rgba(38,40,47,.75),rgba(26,28,34,.75)),#26282f;
  color: var(--text-light-400);
  text-decoration: none;
}

.profile-menu .nav a.active {
  /* background: #31343acc; */
  background: linear-gradient(90deg,rgba(38,40,47,.75),rgba(26,28,34,.75)),#26282f;
  color: var(--text-light-400);
  text-decoration: none;
}

.profile-menu .nav a img {
  margin-right: 10px;
}
.profile-page-content {
  padding: 30px 40px;
  border-radius: 0px 0px 3px 3px;
  height: calc(100% - 58px);
  max-width: 550px;
  width: 100%;
  margin: 0px auto;
}
.profile-page {
  height: 100%;
}
.profile-content .table-dark thead th {
  border-color: transparent;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  padding: 16px 26px;
  color: #99a4b0cc;
}

.profile-grid .tab-content, .profile-grid .tab-content .tab-pane {
  height: 100%;
}

.profile-grid .tab-content .tab-pane .qrCode-section {
  height: calc(100% - 50px);
  display: flex;
  justify-content: center;
  align-items: center;

}


.profile-content .table-dark {
  color: #fff;
}

.profile-content .table-dark.table-striped tbody tr:nth-of-type(odd) {
  background-color: rgb(22 32 43);
}

.profile-content .table-bordered td,
.profile-content .table-bordered th {
  border: none;
}

.profile-content .table-dark.table-striped tbody tr {
  cursor: default !important;
}
.profile-content .table-bordered td {
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  text-align: left;
  color: #99a4b0cc;
  padding: 10px 26px;
  vertical-align: middle;
}
.profile-content .table-dark.table-striped tbody tr td:first-child {
  color: #fff;
}

.profile-content .bet-history-content .table-bordered td {
  padding: 15px 20px;
    min-width: 100px;
}
.profile-wrapper .spinner-border {
  display: inline-block;
  width: 2rem;
  height: 2rem;
  vertical-align: -0.125em;
  border: 3px solid #fbfbfb;
  border-right-color: transparent;
  border-radius: 50%;
  animation: 0.75s linear infinite spinner-border;
}
.profile-wrapper span.visually-hidden {
  color: white;
}
.profile-content
  .bet-history-content
  .table-dark.table-striped
  tbody
  tr
  td:last-child {
  color: #cda73b;
}
.user-profile .form-group {
  width: calc(50% - 10px);
  margin-bottom: 25px !important;
}

.user-profile {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.profile-wrapper .form-control:disabled,
.profile-wrapper .form-control[readonly] {
  background-color: var(--primary-800);
  opacity: 1;
}
.profile-wrapper label {
  font-size: 14px;
  color: var(--text-light-500);
  font-weight: 500;
  margin-bottom: 5px;
}
.profile-wrapper input {
  height: auto;
  padding: 8px 15px;
  background: var(--primary-800);
  border: 1px solid var(--primary-400);
  border-radius: 25px;
  font-size: 14px;
  color: var(--text-light-400);
}

.profile-wrapper input::placeholder {
  color: var(--text-light-400)!important;
}

.profile-wrapper input:focus {
  box-shadow: none;
  background: var(--primary-800);
  /* border: 1px solid #31343acc; */
  color: var(--text-light-500)!important;
}
/* .profile-wrapper input[type="file"] {

} */

.profile-page-content .user-image {
  display: flex;
  justify-content: center;
  margin-bottom: 40px;
  position: relative;
  max-width: 100px;
  margin: 0px auto;
  margin-bottom: 50px;
}
.profile-page-content .user-image img {
  height: 100px;
  width: 100px;
  object-fit: cover;
  border-radius: 50%;
}
.upload-profile input {
  width: 0px;
  visibility: hidden;
  height: 0px;
}

.upload-profile i {
  color: white;
  font-size: 24px;
  cursor: pointer;
}
.upload-profile {
  position: absolute;
  bottom: 12px;
  right: -2px;
}
.page-heading button,
.page-heading button:hover,
.page-heading button:focus,
.page-heading .btn-primary:not(:disabled):not(.disabled):active {
  border: #cda73b !important;
  padding: 5px 20px;
  font-size: 12px;
  color: black;
  background: #cda73b;
  outline: none !important;
  box-shadow: none !important;
  border-radius: 25px;
}
.page-heading button:hover,
.page-heading button:focus {
  filter: drop-shadow(0 0 5px #cda73b);
}

.edit-button {
  display: flex;
  justify-content: center;
  margin-top: 0px;
}

.edit-button button,
.edit-button button:hover,
.edit-button button:focus,
.edit-button .btn-primary:not(:disabled):not(.disabled):active {
  background: #cda73b;
  font-weight: 500;
  height: 38px;
  border: #cda73b;
  font-size: 14px;
  line-height: 14px;
  border-radius: 25px;
  color: #1b1f22 !important;
  outline: none !important;
  box-shadow: none !important;
  padding: 5px 15px;
  min-width: 156px;
}
.edit-button button:hover,
.edit-button button:focus,
.pagination li.next:hover, .pagination li.previous:hover {
  filter: drop-shadow(0 0 5px #cda73b);
  cursor: pointer;
}
.edit-button button:disabled:hover,
.edit-button button.disabled:focus,
.page-heading button:disabled:hover,
.page-heading button.disabled:hover {
  filter: none;
}
.profile-menu .nav a i {
  margin-right: 12px;
  font-size: 17px;
}
/********* setting csss **********/
.settings-btn {
    display: flex;
    justify-content: center;
}
.settings-content {
  padding: 30px 40px;
  border-radius: 0px 0px 3px 3px;
  max-width: 550px;
  width: 100%;
  margin: 0px auto;
}
.settings-content .form-group {
    margin-bottom: 25px !important;
}
.settings-content .form-label {
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  color: white;
  margin-bottom: 5px;
}

.settings-page .form-input {
  position: relative;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
}

.settings-page .form-input img {
  position: absolute;
  top: 14px;
  left: 20px;
}

.settings-page .form-input .form-control::placeholder {
  color: #495766;
}

.settings-page .form-input button,
.settings-page .form-input button:hover,
.settings-page .form-input button:focus {
  background: rgba(31, 46, 62, 0.5);
  border-color: rgba(31, 46, 62, 0.5);
  border-radius: 25px;
  min-width: 122px;
  box-shadow: none;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  height: 40px;
  color: #455b73;
  margin-bottom: 15px;
}

.settings-content .save-btn,
.settings-content .save-btn:hover,
.settings-content .save-btn:focus {
  background: #cda73b;
  border-color: #cda73b;
  border-radius: 25px;
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: #121b24;
  min-width: 156px;
  height: 40px;
  margin-top: 10px;
  box-shadow: none;
}

/********** fairness css **************/
.fairness .form-input .form-control,
.fairness .form-input .form-control:focus {
  padding-left: 20px;
}

.fairness .settings-content .save-btn,
.fairness .settings-content .save-btn:hover,
.fairness .settings-content .save-btn:focus {
  min-width: 122px;
  margin-top: 0;
}

/********* bet history ************/
.bet-history-content {
  padding: 30px 40px;
  border-radius: 0px 0px 3px 3px;
  height: calc(100% - 58px);
}
.bet-history-content .form-group label {
  font-size: 14px;
  color: #ddd;
  font-weight: 500;
  margin-bottom: 5px;
}

.bet-history-content .form-group input {
  height: auto;
  padding: 8px 15px;
  background: #001844;
  border: 1px solid #1d2550;
  border-radius: 4px;
  font-size: 14px;
  color: #a9a9a9;
}
.bet-history-content .pagination {
  justify-content: center;
  margin-top: 20px;
}
.bet-history-content .table-responsive {
  /* height: 400px; */
  overflow: auto;
  position: relative;
  margin-top: 10px;
  min-height: 200px;
  max-height: 500px;
  height: 100%;
}
.bet-history-content .table-responsive thead th {
  color: white;
  font-weight: 500;
}
.pagination li.next.pagination__link--disabled,
li.previous.pagination__link--disabled {
  display: none;
}
.bet-history-content .table-responsive thead {
  position: sticky;
  top: -1px;
  background: var(--primary-600);
}

.bet-history-content .table-responsive::-webkit-scrollbar {
  width: 3px;
  height:3px;
  cursor: pointer;
  }
  .bet-history-content .table-responsive::-webkit-scrollbar-thumb {
    border-radius:6px;
    background: #cda73b !important;
  }
  .bet-history-content .table-responsive::-webkit-scrollbar-track {
    border-radius:6px;
    background: #24262a;
  }
/************ transaction css *************/
.transaction-item {
  display: flex;
  align-items: center;
}

.transaction-item-info h6 {
  margin: 0;
  color: #fff;
  padding-bottom: 4px;
  font-size: 14px;
  line-height: 149.97%;
}
.transaction-item-info span {
  font-weight: normal;
  font-size: 12px;
  line-height: 149.97%;
  display: block;
  color: #99a4b0cc;
}

.transaction-item-info {
  padding-left: 18px;
}

.transaction-item-img img {
  width: 50px;
}

.transaction-value img {
  margin-right: 6px;
}

/*********** pagination css *************/
.pagination button,
.pagination button:hover,
.pagination button:focus {
  background: #212e3b;
  border-radius: 3px;
  border-color: #212e3b;
  min-width: 143px;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  /* identical to box height */
  color: #99a4b0cc;
  padding: 11px 15px;
  text-align: left;
  position: relative;
  box-shadow: none;
}
.pagination button:last-child img {
  margin-left: 10px;
  position: absolute;
  right: 18px;
  top: 14px;
}

.pagination button:first-child img {
  margin-right: 10px;
  position: absolute;
  left: 18px;
  top: 14px;
  transform: rotate(180deg);
}

.pagination button:first-child {
  text-align: right;
}

.pagination button.disabled {
  opacity: 0.5;
  background: #212e3b;
  border-color: #212e3b;
  pointer-events: none;
  color: #99a4b0cc;
}
.pagination-chk {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 10px;
}

.pagination ul li {
  margin: 5px 10px;
  color: #cda73b;
  font-size: 14px !important;
}

.pagination .pagination__link--disabled {
  background: transparent !important;
  padding: 3px 10px;
  border-radius: 5px;
  font-size: 14px !important;
  cursor: not-allowed;
  border: 1px solid #cda73b;
  opacity: 0.8;
}

.pagination .pagination__link--disabled a {
  color: white !important;
  font-weight: 400;
}

.pagination li.next,
.pagination li.previous {
  background: #cda73b;
  padding: 8px 25px;
  border-radius: 25px;
  min-width: 80px;
  text-align: center;
}

.pagination li.next a,
.pagination li.previous a {
  color: black;
  font-weight: 400;
}

.pagination li.pagination__link--active {
  color: white !important;
}
.pagination a {
  text-decoration: none !important;
}

/*********profile page ************/

.bet-history-content .table tr:nth-of-type(odd) {
  background: #31343c !important;
  border: none !important;
}
.bet-history-content .table tr td {
  background: transparent !important;
}
.bet-history-content .table tr:nth-of-type(even) {
  background: transparent !important;
  border: none !important;
}
.bet-history-content .table tr td:first-child {
  border-top-left-radius: 25px !important;
  border-bottom-left-radius: 25px !important;
}
.bet-history-content .table tr td:last-child {
  border-top-right-radius: 25px !important;
  border-bottom-right-radius: 25px !important;
}
/* .bet-history-content .table thead {
  background: transparent !important;
} */
.bet-history-content .table-responsive .table thead tr,
.bet-history-content .table-responsive .table thead th {
  background: transparent !important;
}

/* Mobile Menu */
.profile-mobileMenu {
  display: none;
}

/************************ Responsive Media Queries ******************/

@media (max-width: 1350px) {
  .user-info {
    margin-bottom: 30px;
  }
  .user-name{
    font-size: 16px;
  }
  .user-name img {
    height: 40px !important;
    width: 40px !important;
  }

  .profile-sidebar {
    width: 250px;
  }
  .profile-content {
    width: calc(100% - 280px);
  }

}

@media (max-width: 991px) {
  .profile-sidebar {
    width: 220px;
  }
  .profile-content {
    width: calc(100% - 240px);
  }
}

@media (max-width: 768px) {
  .profile-grid {
    flex-direction: column;
  }
  .profile-menu .nav{
    display: none;
  }
  .profile-sidebar {
    width: 100%;
    display:flex;
    gap: 20px;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  .profile-content {
    width: 100%;
  }
  .profile-menu {
    min-height: fit-content;
    width: 100%;
    padding: 10px;
    display:none
  }
  .user-info {
    margin-bottom: 0;
}
/* Mobile Menu */
.profile-mobileMenu {
  display: block;
  width: 180px;
}
.profile-mobileMenu .dropdown-toggle {
  display: flex;
  gap: 5px;
  justify-content: flex-start;
  align-items: center;
  background: var(--primary-800) !important;
  border: 0 !important;
  box-shadow: none !important;
  outline: 0 !important;
  opacity:  0.9;
  border-radius: 5px;
  padding: 15px 20px;
  width: 100%;
  justify-content: space-between;
  font-weight: 600;
  font-size: 14px;
  line-height: 1;
  color: var(--text-light-500);
}
.profile-mobileMenu .dropdown-toggle:hover,
.profile-mobileMenu .dropdown-toggle:focus,
.profile-mobileMenu .dropdown-toggle:focus-within {
  box-shadow: none !important;
  outline: 0 !important;
  opacity: 1;
}

.profile-mobileMenu .dropdown-menu {
  background: var(--primary-600);
  box-shadow: 0 0 25px rgb(0 0 0 / 25%);
  min-width: 100%;
  border: 0;
  margin-top: 5px;
}

.profile-mobileMenu .dropdown-menu .dropdown-item {
  font-weight: 500;
  font-size: 14px;
  line-height: 1;
  color: var(--text-light-400);
    padding: 12px 15px;
    display: flex;
  gap: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 0 !important;
  box-shadow: none !important;
  outline: 0 !important;
  margin-bottom: 3px;
}
.profile-mobileMenu .dropdown-menu .dropdown-item a {
  color: inherit !important;
  font-size: inherit !important;
  line-height: inherit !important;
}
.profile-mobileMenu .dropdown-menu .dropdown-item:last-child {
  margin-bottom: 0;
}
.profile-mobileMenu .dropdown-menu img {
  width: auto;
    height: 14px;
}
.profile-mobileMenu .dropdown-menu .dropdown-item:hover,
.profile-mobileMenu .dropdown-menu .dropdown-item:focus,
.profile-mobileMenu .dropdown-menu .dropdown-item:focus-within {
  color: var(--text-light-400);
  background: var(--primary-800);
  cursor: pointer;
}


}





@media (max-width: 768px) {
  .bet-history-content {
    padding: 0 20px;
  }
}

@media (max-width: 576px)
{
  .user-profile .form-group {
    width: 100%;
  }
  .user-info {
    padding-left:0;
  }
  .bet-history-content {
    padding: 10px 20px;
  }
}