
.content{
    position: relative;
}
.crash-game-grid {
    padding: 50px 0px 0px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.crash-game-view {
    width: calc(100% - 300px);
    background: var(--primary-base);
    border-radius: 0px 8px 8px 0px;
    /* padding: 14px 14px 14px 0px; */
    padding: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
   overflow: hidden;
}

.crash-game-view:before {
    content: "";
    background: url(../../../assets/images/crash/v4.png);
    /* background: url(../../../assets/images/crash/spaceMoon.jpg); */
    /* background: url(../../../assets/images/crash/spceBgCRash.jpg); */
    /* background: url(../../../assets/images/crash/space-Crash.jpg); */
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: block;
    z-index: 0;
    /* background-repeat: repeat-x;
  animation: slideleft 1000s infinite linear;
  -webkit-animation: slideleft 1000s infinite linear; */
}

@keyframes slideleft {
    from {
      background-position: 0%;
    }
  
    to {
      background-position: 90000%;
    }
  }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}


input[type=number] {
  -moz-appearance: textfield;
}

.crash-game-container{
    position: relative;
}

.crash-game-container h2 {
    margin: 0;
    font-size: 50px;
}

.crash-logo img{
    max-width: 200px;
}
/**************************** Bets ********************************/

.bets {
    width: 100%;
    max-width: 300px;
    background: var(--primary-600);
    background: linear-gradient(90deg,rgba(31,33,39,.75),rgba(26,28,34,.75));
    border-radius: 8px 0px 0px 8px;
    padding: 14px;
}


.bets .all-bets .nav-tabs {
    max-width: 272px;
    margin-bottom: 10px;
    border-radius: 30px;
}

.bets .all-bets .nav-tabs .nav-link.active, .bets .all-bets .nav-tabs .nav-link{
    border-radius: 30px;
}


.bets .all-bets .nav-tabs .nav-link {
    padding: 8px 8px;
}


.bets .all-bets {
    padding: 0px;
}

.bet-form .form-label {
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
/* identical to box height, or 133% */
    color: #FFFFFF;
}

.bet-form .form-control, .bet-form .form-control:focus {
    background: var(--primary-base);
    border-radius: 30px;
    border-color: var(--primary-400);
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
/* identical to box height, or 112% */
    display: flex;
    align-items: center;
    letter-spacing: 0.45px;
    color: #FFFFFF;
    box-shadow: none;
    padding: 6px 15px;
}

.bet-form .form-control::placeholder {
    color: #99a4b0cc;
}

.bet-form .input-group .btn, .bet-form .input-group .btn:hover, .bet-form .input-group .btn:focus {
    background: var(--primary-800);
    border-color: var(--primary-400);
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.45px;
    color: #FFFFFF;
    padding: 10px 8px;
    min-width: 50px;
    width: auto;
    margin: 0;
}

.btn:active:focus{
    box-shadow: none!important;
}

.bet-form .input-group {
    margin-bottom: 10px;
}

.bet-form .btn{
    background: var(--theme-color-main);
    border-radius: 30px;
    border-color: var(--theme-color-main);
    font-size: 14px;
    color: #151823;
    width: 100%;
    padding: 8px 10px;
    box-shadow: none;
    font-weight: 500;
    margin-top: 5px;
    transition: all 0.5s;
}

.bet-form .btn:hover, .bet-form .btn:focus, .bet-form .btn:focus-within, .bet-form .btn:disabled {
    background: #cda73b;
    border-color: #cda73b;
    box-shadow: none;
    color: #151823;
}

.bet-list .table thead tr:first-child {
    background: transparent;
}
.bet-form .btn-sbmit:hover {
    background: var(--theme-color-main);
    border-color: var(--theme-color-main);
    filter: drop-shadow(0px 0px 5px var(--theme-color-main));
}


.bet-form .bet-list .table tr:nth-of-type(odd) {
    background: transparent;
}

.bet-form .bet-list .table td {
    padding: 10px 6px;
    font-size: 14px;
}

.bet-form .bet-list {
    background: #26282e;
    border-radius: 6px;
    margin-top: 5px;
    max-height: 180px;
    overflow: auto;
}
.bet-total-amount{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 16px; 
}


.bet-total-amount span {
    color: #99a4b0cc;
    font-size: 14px;
    font-weight: 500;
}

.bet-total-amount span:last-child {
    color: var(--theme-color-main);
}

/**************************** All Bets ****************************/

.all-bets{
    padding: 80px 0px;
}

.all-bets .nav-tabs {
    background: var(--primary-base);
    border-radius: 30px;
    border: none;
    margin-bottom: 30px;
    display: flex;
    padding: 5px 6px 6px;
    max-width: 402px;
}
.all-bets-table-section .nav-tabs {
    background: var(--primary-600);
}

.all-bets .nav-tabs .nav-item {
    
    
    
/* identical to box height, or 114% */
    max-width: 130px;
    width: 100%;
    text-align: center;
}

.all-bets .nav-tabs .nav-link {
    text-align: center;
    width: 100%;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #99a4b0cc;
    border-radius: 30px;
    border: none;
    padding: 15px 8px;
}

.all-bets .nav-tabs .nav-link.active {
    background: var(--primary-800);
    border-radius: 30px;
    border: none;
    color: #fff;
}

.all-bets .nav-tabs .nav-link:hover {
    border: none;
}


.all-bets .table {
    margin: 0;
    position: relative;
}
.all-bets .table thead {
    position: sticky;
    top: 0;
    background: var(--primary-base);
}

.bet-list .table-responsive {
    max-height: 210px;
}
.all-bets.all-bets-table-section .table-responsive {
    max-height: 530px;
}
.bet-list .table-responsive::-webkit-scrollbar {
width:4px;
height: 4px;
}
.bet-list .table-responsive::-webkit-scrollbar-thumb {
    border-radius: 6px;
    background: rgb(230, 190, 75, 0.6);
}
.bet-list .table-responsive::-webkit-scrollbar-track {
    border-radius: 6px;
    background: #333333;
}

.bet-list .table tr th {
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: #FFFFFF;
    padding: 15px 20px;
    background: transparent;
    border: none;
}

.bet-list .table td {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--text-light-400);
    padding: 16px 20px;
    vertical-align: middle;
    background: transparent;
    border: none;
}

.bet-list .table tr:nth-of-type(odd) {
    background: var(--primary-800);
    overflow: hidden;
}

.bet-list .table tr td:first-child{
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
}

.bet-list .table tr td:last-child{
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
}

.game-box img, .bet-amount img, .payout img, .game-box svg, .bet-amount svg, .payout svg {
    width: 16px;
    margin-right: 5px;
}
.bet-amount, .payout{
    display: flex;
    align-items: center;
}
.game-box {
    font-weight: 600;
    color: var(--text-light-500);
    display: flex;
    align-items: center;
}

.payout.green-text{
   color: #00E701;
}
/****** autobet section **********/
.profit-loss-section .form-group, .auto-cashout .form-group {
    width: 49%;
}

.profit-loss-section, .auto-cashout {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}
/* .win-section .input-group button:nth-child(2),
.loss-section .input-group button:nth-child(2)
{
    background-color: transparent !important;
} */
.auto-bet .win-section button:last-child, .auto-bet .win-section button:last-child:hover,
.auto-bet .loss-section button:last-child, .auto-bet .loss-section button:last-child:hover
{
    background: #cda73b;
    border-radius: 5px !important;
    border-top-left-radius: 0px !important;
    color: #000;
    border-bottom-left-radius: 0px !important;
}
.profit-loss-section button i {
    font-size: 22px !important;
}

.profit-loss-section button {
    padding-top: 6px !important;
    padding-bottom: 6px !important;
}

/* ************************Crash iframe**************  */
iframe {
    width: 100%;
    min-height: 450px;
    border-radius: 8px;
    z-index: 1;
  }
  
  @media (max-width: 1350px) {
    .crash-page iframe {
      min-height: 350px;
    }
  }
  
  @media (max-width: 992px) {
    .crash-page iframe {
      min-height: 400px;
    }
  }
  
  @media (max-width: 576px) {
  .crash-page iframe {
      min-height: 210px;
    }
  }

/************************** Meadia Query **************************/

@media screen and (max-width:767px){
.crash-game-grid{
    flex-wrap: wrap;
    
    padding: 20px 0px 0px;

}

.bets{
   max-width: 100%;
    width: 100%;
    order: 2;
    border-radius: 0px 0px 8px 8px;
}

.crash-game-view{
    width: 100%;
    order: 1;
    min-height: 300px;
    border-radius: 8px 8px 0px 0px;
}

.all-bets .nav-tabs .nav-item {
    max-width: 33.33%;
}
.all-bets .nav-tabs .nav-link {
    font-size: 14px;
    padding: 10px 5px;
}

.bets .all-bets .nav-tabs {
    max-width: 100%;
}
.crash-game-grid .all-bets .nav-tabs .nav-item {
    max-width: 50%;
}
.all-bets .nav-tabs {
    margin-bottom: 15px;
}
.bet-list .table td{
    min-width: 95px;
}
.bet-list .table tr th {
    font-size: 14px;
}
}