.register-form {
  max-width: 450px;
  margin: 0px auto;
  padding: 0px 0px;
  position: relative;
}

.register-form img {
  max-width: 250px;
  margin: 0px auto;
  display: block;
}

.head-logo img {
  max-width: 100px;
  margin: 0px auto;
  display: block;
  border: 2px solid #ff5917;
  border-radius: 10px;
  padding: 5px;
  margin-bottom: 20px;
}

.register-form h1 {
  font-size: 32px;
  text-align: center;
  max-width: 400px;
  margin: 0px auto;
  color: #fff;
  margin-bottom: 0.5rem;
}

.register-form p {
  text-align: center;
  color: #99a4b0cc;
  font-size: 14px;
  max-width: 270px;
  margin: 0px auto;
}

.register-form h1 span {
  color: var(--theme-color-main);
}

.register-form .rigister-tab {
  margin-top: 20px;
  margin-bottom: 0px;
}

.terms-link {
  color: #99a4b0cc;
  text-decoration: underline;
}

.terms-link:hover {
  color: var(--theme-color-main);
}

.dis-show .form-check {
  display: inline-block;
}

.dis-show p {
  font-size: 13px;
  color: #99a4b0cc;
  margin: 0;
  text-align: left;
}

.dis-show {
  display: flex;
  align-items: flex-start;
}

.s-btn.btn.btn-primary {
  padding: 8px 40px;
  font-size: 14px;
  background: var(--theme-color-main);
  border-radius: 30px;
  font-weight: 600;
  color: var(--primary-base);
  border: none;
  transition: all 0.5s;
  width: 100%;
  margin-top: 15px;
}

.s-btn.btn.btn-primary:hover,
.s-btn.btn.btn-primary:focus,
.s-btn.btn.btn-primary:active {
  background: var(--theme-color-main);
  box-shadow: none;
  border: none;
}
.s-btn.btn.btn-primary:hover {
  filter: drop-shadow(0px 0px 5px var(--theme-color-main));
}

.register-form .form-group {
  margin-bottom: 15px;
}

.register-form .form-group .form-control {
  height: auto;
  padding: 8px 15px;
  background: var(--primary-600);
  border: 1px solid var(--primary-400);
  border-radius: 30px;
  font-size: 14px;
  color: #99a4b0cc;
  width: 100%;
}

.register-form .form-group .form-control::placeholder {
  color: #99a4b0cc;
}

.register-form .form-group .form-control:focus {
  box-shadow: none;
  background: var(--primary-600);
}

.register-form input::-internal-autofill-selected {
  background: transparent !important;
}

.form-control:-webkit-autofill,
.form-control:-webkit-autofill:hover,
.form-control:-webkit-autofill:focus {
  border: 1px solid var(--primary-600);
  -webkit-text-fill-color: #ffffff;
  -webkit-box-shadow: 0 0 0px 1000px var(--primary-600) inset;
  transition: background-color 5000s ease-in-out 0s;
  outline: none;
}

.signup .form-label {
  font-size: 14px;
  color: #fff;
  font-weight: 500;
  margin-bottom: 5px;
}

.already {
  text-align: center;
  padding-top: 20px;
  padding-bottom: 10px;
  font-size: 14px!important;
  color: #99a4b0cc;
}

.signup {
  position: relative;
}

input[type="date"].form-control,
input[type="datetime-local"].form-control,
input[type="month"].form-control,
input[type="time"].form-control {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  position: relative;
}

input[type="date"]:before {
  background: none;
  display: block;
  font-family: "FontAwesome";
  content: "\f073";
  width: 15px;
  height: 20px;
  position: absolute;
  top: 8px;
  right: 15px;
  color: #99a4b0cc;
  cursor: pointer;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

.signup-popup .modal-header {
  border-bottom: none;
  padding-bottom: 0;
}

.signup-popup .modal-header .close:focus {
  outline: none;
}

.signup-popup .close {
  color: #99a4b0cc;
  text-shadow: none;
  font-weight: 400;
  font-size: 30px;
  cursor: pointer;
}

.signup-popup .close:hover {
  color: #efebeb;
}

.already {
  text-align: center;
  padding-top: 30px;
  font-size: 13px;
  color: #99a4b0cc;
}

.signup-popup .modal-content {
  position: relative;
  background-size: cover;
  background-position: center;
  position: relative;
  background: var(--primary-base);
}

.signup-popup .modal-body {
  padding-top: 0;
}


.signup-popup .modal-dialog{
  max-width: 475px;
}

.error-text {
  text-align: left!important;
  color: #ea4444!important;
  max-width: 100%!important;
  font-size: 13px!important;
  font-weight: 500!important;
  padding: 5px 0px 0px!important;
  margin: 0!important;
}

.error-field.form-control {
  border-color: #ea4444!important;
}

.success-msg{
  text-align: center;
}

.half-width {
  display: flex;
  justify-content: space-between;
}

.half-width .form-group {
  width: 48%;
}


.register-form .react-tel-input .country-list .country-name {
  color: #fff;
}

.register-form .react-tel-input .country-list .country .dial-code {
  color: #fff;
}

.register-form .react-tel-input .selected-flag:focus:before, .register-form .react-tel-input .selected-flag.open:before{
  display: none;
}

.register-form .react-tel-input .country-list{
  width: 200px;
  background-color: #26282e;
}


.register-form .react-tel-input .country-list::-webkit-scrollbar-track
{
	border-radius: 10px;
	background-color: var(--primary-base);
}

.register-form .react-tel-input .country-list::-webkit-scrollbar
{
	width: 0px;
	background-color: var(--primary-base);
}

.register-form .react-tel-input .country-list::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	background-color: var(--primary-base);
}


.register-form .react-tel-input .country-list .country:hover, .register-form .react-tel-input .country-list .country.highlight {
  background-color: var(--primary-base);
}

.register-form .react-tel-input .form-control {
  padding-left: 50px!important;
}

.success-msg {
  text-align: center;
  font-size: 18px;
  margin: 0;
  padding-top: 20px;
  color: #56d538;
}