@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap";
@import "https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap";

:root {
--theme-color-main: #e6be4b;
--primary-400 : #363943;
--primary-500: #26282f;
--primary-600 : #1a1c22;
--primary-800: #1f2127;
--primary-base : #16181e;
--text-light-400: #8d8e92;
--text-light-300: #8d8d9180;
--text-light-500: #D5DCEB;
/* --primary-base: #020226;
    --primary-400: #072273;
    --primary-600: #000113;
    --primary-800: #000014; */
}

body {
  font-family: 'Poppins', sans-serif!important;
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none; 
  font-weight: 400;
  overflow-x: hidden;
  color: #fff!important;
  background: var(--primary-base)!important;
}

.container{
  max-width: 1230px!important;

}

.main-wrapper{
  position: relative;
}

/* .main-wrapper:before{
  content: "";
  background: url(./assets/images/crash/main-bg.png);
  background-attachment: fixed;
  background-size: cover;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
} */

.main-wrapper:after {
  content: "";
  background: #16181e;
  /* background: linear-gradient(90deg,#1a1c22,#363943),#1f2127;
  background: linear-gradient(90deg,rgba(38,40,47,.75),rgba(26,28,34,.75)),#26282f; */
  background-attachment: fixed;
  background-size: cover;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.wrapper {
  width: calc(100% - 350px);
  margin: 0px auto;
  margin-right: 350px;
  transition: 0.4s all ease;
  position: relative;
  z-index: 9;
}

.wrapper-hide .wrapper {
  margin-right: 0px;
  width: 100%;
  transition: 0.4s all ease;
}

.loader{
  position: fixed;
  width: 100%;
  background: #24262b;
  background: linear-gradient(90deg,#1a1c22,#363943),#1f2127;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.form-control:focus {
  border: 1px solid var(--text-light-300) !important;
  box-shadow: none !important ;
}



/************************************************************************************************************/

                           /*************** Media Query ******************/

/************************************************************************************************************/


@media screen and (max-width:991px) {
  .wrapper {
    width: 100%;
  }
}



@media screen and (max-width:767px){

.container {
  padding: 0px 20px;
}

}