.widthdraw-deposit-page {
	padding: 50px 0px;
	/* width: 50%; */
	margin: 0px auto;
}
.widthdraw-deposit-page .tab-content {
	/* background: linear-gradient(180deg, #001a47, #03051a); */
	/* padding: 40px 35px; */
	padding: 0;
	min-height: 400px;
}
.widthdraw-deposit-page .tab-content .tab-pane .user-withdraw-page, .widthdraw-deposit-page .tab-content .tab-pane .user-deposit {
	padding: 40px 35px;
}

.widthdraw-deposit-page label {
	font-size: 14px;
	color: #ddd;
	font-weight: 500;
	margin-bottom: 5px;
}
.widthdraw-deposit-page input,
.widthdraw-deposit-page .form-control {
	height: 42px;
  padding: 8px 15px;
  background: var(--primary-800);
  border: 1px solid var(--primary-400);
  border-radius: 25px;
	font-size: 14px;
	color: #a9a9a9;
}
.widthdraw-deposit-page input::placeholder {
	color: #a9a9a9;
}

.widthdraw-deposit-page input:focus {
	box-shadow: none;
	background: var(--primary-800);
  border: 1px solid var(--primary-400);
}
.widthdraw-deposit-page .form-group {
	width: 48%;
}

/* .widthdraw-deposit-page .user-deposit-box{
