    flex-wrap: wrap;
} */
.widthdraw-deposit-page .deposit-btn {
	display: flex;
	justify-content: flex-end;
	margin: 30px 0px 0px;
}
.widthdraw-deposit-page .text-danger {
	font-size: 12px;
}
.widthdraw-deposit-page .nav-tabs {
	border-bottom: none;
}
.widthdraw-deposit-page .nav .nav-item a {
	padding: 15px 16px;
}
