.bar-code-secion {
	display: flex;
	justify-content: center;
	align-items: center;
}

.bar-code-secion img {
	height: 150px;
	margin-bottom: 10px;
}
.assets-section {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 20px 0px;
	/* border: 1px solid #011c4d; */
	border-left: none;
	border-right: none;
	margin: 20px 0px;
}
.assets-section .asstes-left img {
	height: 30px;
}
.user-deposit {
	width: 100%;
}

.assets-section img:last-child {
	filter: invert(1);
	height: 26px;
	cursor: pointer;
}

.asstes-left {
	display: flex;
	align-items: center;
}

.asstes-left .bitxoin-heading p {
	margin-bottom: 0px;
	color: #c7c7c7;
	font-size: 12px;
}

.asstes-left .bitxoin-heading {
	margin-left: 15px;
}

.asstes-left .bitxoin-heading h6 {
	color: white;
	margin-bottom: 0px;
	font-family: 'Poppins';
}
.btc-address-section {
	border-top: none;
	padding-top: 0px;
	margin-bottom: 0px;
}
.btc-address-section i {
	color: white;
	cursor: pointer;
}
.user-deposit-box {
	padding: 30px 0px 0px;
	/* border: 1px solid #011c4d; */
	border-bottom: none;
}
.bitcoin-info {
	display: flex;
	color: white;
	font-size: 12px;
	margin-top: 12px;
}

.bitcoin-info i {
	margin-right: 10px;
	font-size: 18px;
}
.bitcoin-balance {
	display: flex;
	justify-content: space-between;
	color: white;
	font-size: 1em;
	margin-top: 10px;
}
.bitcoin-balance p {
	margin: 0;
	color: var(--text-light-500);
}
.user-withdraw-page {
	width: 100%;
}

.withdraw-balance .withdraw-amount {
	text-align: center;
}

.withdraw-balance .withdraw-amount i {
	font-size: 22px;
	color: white;
	margin-right: 8px;
	position: absolute;
	margin-left: -16px;
	top: 10px;
}

.withdraw-balance .withdraw-amount {
	color: white;
	font-size: 40px;
	display: contents;
	font-weight: 700;
	position: relative;
	display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}
.withdraw-balance .withdraw-amount .amountSection {
	display: flex;
    align-items: center;
    justify-content: flex-start;
    max-width: 100%;
    flex-direction: column;
    width: fit-content;
    gap: 10px;
}
.withdraw-balance .withdraw-amount .amountSection h4 {
	margin:0;
	font-size: 14px;
    font-weight: 500;
    margin-bottom: 0;
	color: var(--text-light-500);
}
.withdraw-balance p {
	color: white;
	font-size: 12px;
	margin-top: 15px;
	text-align: center;
}
.withdraw-balance {
	padding: 10px 0px;
}
/* .withdraw-list.pay-with {
    display: flex;
    justify-content: flex-start;
    align-items: center;
} */

.withdraw-list.pay-with .withdraw-field {
	width: 100%;
	display: flex;
	justify-content: space-between;
}

.withdraw-list p {
	margin-bottom: 3px;
}

.select-blockchain {
	height: 42px;
  padding: 8px 15px;
  background: var(--primary-500);
	border-radius: 5px;
	cursor: pointer;
	border-radius: 25px;
}

.withdraw-list.pay-with .withdraw-field img {
	filter: invert(1);
	height: 26px;
	cursor: pointer;
}
.withdraw-details {
display: flex;
gap: 15px;
justify-content: space-between;
}
.withdraw-list {
	color: white;
	margin-bottom: 0px;
	font-size: 13px;
	font-weight: 500;
	padding: 15px 0px;
	width: calc(100% - 8px);
}
/* . .withdraw-list{
    border-bottom: 1px solid #07255b;
} */
.withdraw-list.pay-with {
	border-bottom: none;
}
.withdraw-list.pay-with .withdraw-field > div img {
	margin-right: 10px;
	filter: inherit;
}

.widthdraw-deposit-page .deposit-btn {
	width: 100%;
	margin: 20px auto;
}

.widthdraw-deposit-page .deposit-btn button,
.widthdraw-deposit-page .deposit-btn button:hover,
.widthdraw-deposit-page .deposit-btn button:focus,
.widthdraw-deposit-page .deposit-btn button:focus-within {
	width: 100%;
	max-width: 200px;
	height: 47px;
	margin:auto;
	padding: 8px 40px;
    font-size: 14px;
    background: var(--theme-color-main);
    border-radius: 30px;
    font-weight: 600;
    color: var(--primary-base);
    border: none;
    transition: all 0.5s;
    width: 100%;
}
.widthdraw-deposit-page .deposit-btn button:hover {
	filter: drop-shadow(0px 0px 5px var(--theme-color-main));
}

.assets-section.btc-address-section .asstes-left .bitxoin-heading {
	margin-left: 0;
}

/********** assets popup **********/
.assets-popup .assets-section {
	margin: 5px 0px;
	cursor: pointer;
	padding: 20px 10px;
	transition: all 0.2s;
}

.assets-popup .assets-section .asstes-left.assets-right h6 {
	font-weight: 500 !important;
	font-size: 12px;
}
.assets-popup .modal-title.h4 {
	color: white;
	font-size: 18px;
	font-weight: 500;
}
.assets-popup .assets-listing-section {
	margin-top: 30px;
}
.assets-popup .assets-section .asstes-left img {
	height: 30px;
	border-radius: 50%;
}
.assets-popup .assets-section:hover {
	background: #b1b9c017;
	border-radius: 6px;
}
.info-popup .assets-listing-section p {
	color: white;
	font-size: 14px;
	margin: 30px 10px;
}
.info-popup .modal-title.h4 {
	color: white;
	font-size: 18px;
	font-weight: 500;
	padding-left: 10px;
}
.info-popup .edit-button button {
	max-width: 120px;
	width: 100%;
	margin-top: 20px;
}
.tick {
	filter: initial !important;
}
.withdraw-amount input {
	width: 50% !important;
    max-width: 200px;
	text-align: left;
	background: none;
	border: none;
	font-weight: 600;
	font-size: 18px;
    line-height: 1;
    color: var(--text-light-400);
    padding: 10px 12px;
    height: 40px;
	background: var(--primary-800);
    border: 1px solid var(--primary-400);
}
.withdraw-amount input:focus,
.withdraw-amount input:hover {
	outline: none;
	background: none;
	box-shadow: none;
	background: var(--primary-800);
    border: 1px solid var(--primary-400);
}
.withdraw-amount input:focus {
    border: 1px solid var(--text-light-300);
}


.user-deposit .back-arrow{
	transform: rotate(180deg);
	height: 40px;
	cursor: pointer;
}