.forget-form {
  max-width: 350px;
  margin: 0px auto;
  padding: 0px 0px;
  position: relative;
}

.forget-form h1 {
  font-size: 32px;
  text-align: center;
  color: #fff;
}

.forget-form .form-group .form-control {
  height: auto;
  padding: 8px 15px;
  background: #31343c66;
  border: 1px solid #31343c66;
  border-radius: 30px;
  font-size: 14px;
  color: #99a4b0cc;
}

.forget-form .form-group .form-control::placeholder {
  color: #99a4b0cc;
}

.forget-form .form-group .form-control:focus {
  box-shadow: none;
  background: #31343c66;
}

.login-box input:-internal-autofill-selected {
  background: transparent !important;
}

.forget-form p {
  text-align: center;
  color: #99a4b0cc;
  font-size: 14px;
}

.forget-box {
  max-width: 450px;
  margin: 20px auto 20px;
}

.forget-form .reset-btn {
  background: #025b95;
  border: none;
  transition: all 0.5s;
  padding: 10px 40px;
  font-size: 14px;
  font-weight: 500;
  color: #fff;
  border-radius: 30px;
  margin: 0px auto;
  display: block;
}

.reset-box {
  max-width: 450px;
  margin: 50px auto;
  margin-bottom: 0;
}

.forget-form .form-group {
  margin-bottom: 15px;
}

.forget-form .form-label {
  font-size: 14px;
  color: #fff;
  font-weight: 500;
  margin-bottom: 5px;
}


.verify-email-section .forget-section {
  background: var(--primary-600);
  max-width: 400px;
  margin: 0px auto;
  width: 100%;
  padding: 40px;
  border-radius: 6px;
}

.verify-email-section {
  min-height: calc(100vh - 242px);
  display: flex;
  align-items: center;
  justify-content: center;
}
